___CSS_LOADER_EXPORT___.push([module.id, `.tabsContainer .active{
  background-color: red;
}

.previewContainer {
  display: flex;
  align-items: center;
}

.previewContainer div {
  margin: 0 8px;
}

.preview {
  width: 50px;
  height: 50px;
  border: 1px solid #CCCCCC;
  display: inline-block;
  border-radius: 8px;
}

.tooltip {
  background-color: rgba(0, 0, 0, 0.8);
  color: #fff;
  padding: 8px;
  border-radius: 4px;
  z-index: 999;
  margin: 0;
  font-size: .9rem;
}

/* Tooltip container */
.tooltip {
  position: relative;
  display: inline-block;
  border-bottom: 1px dotted black; /* If you want dots under the hoverable text */
  
}

/* Tooltip text */
.copy_icon .tooltiptext {
  visibility: hidden;
  width: 120px;
  background-color: #555;
  color: #fff;
  text-align: center;
  padding: 5px 0;
  border-radius: 6px;

  /* Position the tooltip text */
  position: absolute;
  z-index: 1;
  bottom: 125%;
  left: 50%;
  margin-left: -60px;

  /* Fade in tooltip */
  opacity: 0;
  transition: opacity 0.3s;
}

/* Tooltip arrow */
.copy_icon .tooltiptext::after {
  content: "";
  position: absolute;
  top: 100%;
  left: 50%;
  margin-left: -5px;
  border-width: 5px;
  border-style: solid;
  border-color: #555 transparent transparent transparent;
}

/* Show the tooltip text when you mouse over the tooltip container */
.copy_icon:hover .tooltiptext {
  visibility: visible;
  opacity: 1;
}

.grid {
  content: "";
  display: block;
  position: relative;
  z-index: -1;
  top: 0;
  left: -8px;
  width: 50px;
  height: 50px;
  border-radius: 8px;
  background-image: url(${___CSS_LOADER_URL_REPLACEMENT_0___});
  background-size: contain;
}

.textColor{
  padding: 10px;
  display: inline-block;
  border-radius: 10px;
  background-color: #F0F0F0;
}

.copy_icon:hover{
  cursor: pointer;
}

.textColor:hover{
  background-color: #D4D9E8;
}

input{
  width: 100%;
  background-color : rgb(247, 249, 252);
  padding-left: 8px;
  border: 1px solid rgb(237, 241, 247);
  border-radius: 4px;
  color: rgb(25, 32, 56);
  font-size: 15px;
  font-weight:  600;
  height: 40px;
  letter-spacing: normal;
  line-height: 24px;
